<template>
  <div class="float-button-content" :style="position"
  @mousedown.stop="down" @touchstart.stop="down"
  @mousemove.stop="move" @touchmove.stop="move"
  @mouseup.stop="end" @touchend.stop="end"
  @click="click">
    <img src="data:image/png;base64," alt="">
    <span>{{btnText}}</span>
  </div>
</template>
<script>
export default {
  name: 'float-button',
  props: {
    // 默认位置
    initialY: {
      type: Number,
      default: 200
    },
    btnText: {
      type: String,
      default: '返回直播'
    }
  },
  data() {
    return {
      positionY: this.initialY,
      preY: this.initialY,
      touchDownFlag: false,
      touchStartY: 0,
    }
  },
  computed: {
    position() {
      return { top: this.positionY + 'px', }
    }
  },
  methods: {
    click() {
      this.$emit('click')
    },
    down(event) {
      if (event.touches && event.touches.length >= 1) {
        this.touchDownFlag = true
        this.touchStartY = event.touches[0].clientY
      }
    },
    move(event) {
      if (event.touches && this.touchDownFlag && event.touches.length >= 1) {
        let currentY = event.touches[0].clientY
        let offsetY = currentY - this.touchStartY
        let result = this.preY + offsetY
        if (result < 0 || result > document.body.clientHeight - 100) {
          return
        }
        this.positionY = result;
      }
    },
    end(event) {
      this.preY = this.positionY
      this.touchDownFlag = false
    }
  },
}
</script>
<style scoped>
.float-button-content{
  width: 1.74rem;
  height: 0.84rem;
  position: fixed;
  z-index: 100;
  background-color: rgba(0,0,0,0.6);
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items:center;
  border-top-left-radius: 0.42rem;
  border-bottom-left-radius: 0.42rem;
  right: 0;
}
.float-button-content img{
  width: 0.36rem;
  height: 0.32rem;
  padding-left: 0.2rem;
}
.float-button-content span{
  display: block;
  color: #fff;
  font-family:PingFangSC-Regular,PingFang SC;
  font-size:0.24rem;
  font-weight:400;
  line-height: 0.36rem;
  height: 0.36rem;
}

</style>
